/* ====== PAGE STRUCTURE ====== */

/* --- Main Content Wrapper --- */
.page-content {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto; /* Fills available space */
  /* Remove side paddings for full-width widget */
  padding-right: 0;
  padding-left: 0;
  margin-bottom: var(--page-margin-auto);
  box-sizing: border-box;
  /* align-items: center; */
  min-height: 0;
}

/* === Reusable Main Layout Container === */
.main-layout {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
  padding-right: var(--padding-right-left);
  padding-left: var(--padding-right-left);
  margin-bottom: var(--page-margin-auto);
  box-sizing: border-box;
  min-height: 0;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

/* ====== HEADER & SECTION UTILITIES ====== */

/* --- Header Wrapper --- */
.header-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 2.5rem;
  margin-bottom: 1.2rem; /* reduced from 2.5rem for tighter spacing */
  padding-right: var(--padding-right-left);
  padding-left: var(--padding-right-left);
  gap: 1.2rem;
}

/* --- Header Title --- */
.header-title {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
  font-size: clamp(2.8rem, 7vw, 4.5rem);
  font-weight: 900;
  letter-spacing: 0.04em;
  line-height: 1.05;
  margin: 0;
  padding: 0 0.5rem;
  text-shadow: 0 2px 24px #af00ff55, 0 0 8px #6cffe655;
  margin-bottom: 0.01rem;
  gap: 0.08em;
}

/* --- Header Symbol --- */
.header-symbol {
  font-size: clamp(3.1rem, 8vw, 4.8rem);
  margin-right: 0.03em;
  vertical-align: baseline;
  line-height: 1;
  display: inline-block;
  text-shadow: 0 2px 24px #af00ff99, 0 0 8px #6cffe655;
}

/* --- Header Tagline --- */
.header-tagline {
  font-size: clamp(1.1rem, 3vw, 2.1rem);
  font-weight: 400;
  display: block;
  text-align: center;
  margin-top: 0.005rem;
  margin-bottom: 0;
  letter-spacing: 0.02em;
  color: var(--color-purple);
  text-shadow: 0 2px 16px #af00ff33, 0 0 4px #6cffe633;
}

/* --- Heartbeat Container --- */
.heartbeat-container {
  display: flex;
  flex: 1 0 auto;
  width: 100%;
  max-width: 100%;
  position: relative;
  left: 0;
  right: 0;
  transform: none;
  height: 100%;
  justify-content: center;
  text-align: center;
  align-items: center;
  padding-right: 0;
  padding-left: 0;
  margin-top: 3rem;
  margin-bottom: 3rem;
}

/* --- Paragraph Container --- */
.paragraph-container {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; 
  padding-right: var(--padding-right-left);
  padding-left: var(--padding-right-left);
  margin-top: var(--margin-top-bottom);
  margin-bottom: 2rem;
}

.paragraph-brick{
    display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;

}

/* --- Links Container --- */
.links-container {
  display: flex;
  flex: 1 0 auto;
  font-size: .75rem;
  justify-content: center;
  text-align: center;
  align-items: top;
  padding-right: var(--padding-right-left);
  padding-left: var(--padding-right-left);
  margin-top: var(--margin-top-bottom);
  margin-bottom: var(--margin-top-bottom);
}

/* --- Unordered List --- */
.unordered-list {
  display: flex;
  flex-direction: column;
  padding-right: var(--padding-right-left);
  padding-left: var(--padding-right-left);
  margin-top: var(--margin-top-bottom);
  margin-bottom: var(--margin-top-bottom);
}

/* --- List Item --- */
li {
  display: flex;
  flex-direction: column;
  margin-top: var(--margin-top-bottom);
  margin-bottom: var(--margin-top-bottom);
}

/* --- Action Button Group --- */
.action-btn-group {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding-right: var(--padding-right-left);
  padding-left: var(--padding-right-left);
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.action-btn-group .button {
  width: 200px; 
  min-width: 200px;
  max-width: 200px;
  height: 100%;
}

/* --- Back Link Container --- */
.back-link-container {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  padding-right: var(--padding-right-left);
  padding-left: var(--padding-right-left);
  margin-top: var(--margin-top-bottom);
  margin-bottom: var(--margin-top-bottom);
}

/* --- Bottom Divider --- */
.bottom-divider {
  padding-right: var(--padding-right-left);
  padding-left: var(--padding-right-left);
  margin-top: var(--margin-top-bottom);
  margin-bottom: 2rem;
}

/* --- Heartbeat SVG --- */
.heartbeat-svg {
  width: 100%;
  max-width: 100%;
  min-width: 320px;
  display: block;
}

/* --- Uniswap Widget Container --- */
.uniswap-widget-container {
  width: 100%;
  max-width: 100%;
  min-width: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  padding: 0;
}

.uniswap-widget-container iframe {
  width: 100% !important;
  max-width: 100% !important;
  min-width: 0 !important;
  display: block;
  box-sizing: border-box;
}




