/* ====== MENU ICON BUTTON ====== */
.menu-icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 38px;
  height: 38px;
  cursor: pointer;
  z-index: 6000;
  background: none;
  border: none;
  margin-left: auto; /* Push to right */
  margin-right: 0;   /* Align flush right */
  position: absolute;
  right: 1.5rem;
  top: 6px;
}

/* ====== MENU ICON BARS ====== */
.menu-icon-bar {
  width: 28px;
  height: 4px;
  background: var(--color-purple);
  margin: 3px 0;
  border-radius: 2px;
  transition: background 0.1s;
}

/* ====== MENU ICON OPEN STATE (ANIMATION) ====== */
.menu-icon--open .menu-icon-bar:nth-child(1) {
  transform: translateY(7px) rotate(45deg);
}
.menu-icon--open .menu-icon-bar:nth-child(2) {
  opacity: 0;
}
.menu-icon--open .menu-icon-bar:nth-child(3) {
  transform: translateY(-7px) rotate(-45deg);
}

/* ensure nav container is positioned for the absolute menu */
.navbar {
  position: relative;
}

/* hide hamburger by default */
.menu-icon {
  display: none;
}

/* mobile styles */
@media (max-width: 600px) {
  /* hide desktop links */
  .navbar-links {
    display: none;
    flex-direction: column;
    background: #000 !important;
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    border-bottom-left-radius: 24px;
    border-bottom-right-radius: 24px;
    box-shadow: 0 8px 32px #af00ff33, 0 2px 8px #6cffe633;
    padding: 2.2rem 1.5rem 2.5rem 1.5rem;
    z-index: 5000;
    animation: navbar-popdown 0.32s cubic-bezier(.68,-0.55,.27,1.55);
    border-bottom: 2px solid var(--color-purple);
    gap: 1.2rem;
    align-items: center;
  }

  /* show links when open */
  .navbar-links.navbar-links--open {
    display: flex;
  }

  /* spacing for mobile links */
  .navbar-links li {
    margin: 0.5rem 0;
    width: 100%;
    text-align: center;
    border-radius: 12px;
    transition: background 0.18s;
  }

  .navbar-links li:hover {
    background: #222;
  }

  /* link styles */
  .navbar-link {
    font-size: 1.15rem;
    font-weight: 700;
    letter-spacing: 0.03em;
    color: var(--color-teal);
    text-shadow: 0 2px 12px #6cffe655, 0 0 4px #af00ff33;
    padding: 0.7rem 0;
    display: block;
    border-radius: 10px;
    transition: color 0.18s, background 0.18s;
  }

  .navbar-link:hover {
    color: var(--color-purple);
    background: #181818;
  }

  @keyframes navbar-popdown {
    0% {
      opacity: 0;
      transform: translateY(-24px) scaleY(0.95);
    }
    100% {
      opacity: 1;
      transform: translateY(0) scaleY(1);
    }
  }

  /* show hamburger icon */
  .menu-icon {
    display: flex;
  }
}


