/* ====== BUTTON STYLES ====== */

/* --- Base Button Styles --- */
button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: var(--font-main);
  cursor: pointer;
  background: none;
  border: none;
  overflow: hidden;
  border-radius: 2rem;

}

/* --- Button Hover and Active States --- */
button.button-primary:hover {
  filter: drop-shadow(0 0 4px var(--color-teal));
}
button:active {
  transform: scale(0.95);
}
button {
  filter: drop-shadow(0 0 4px var(--color-teal));
}

/* ====== PRIMARY BUTTON ====== */
.button-primary {
  background: var(--color-teal, var(--color-teal)); /* Fallback for undefined variable */
  color: var(--color-purple, var(--color-purple));
  box-shadow: 0 0 24px var(--color-teal, var(--color-teal)),
              0 0 8px var(--color-purple, var(--color-purple));
  /* Ensure shadows and filters render on mobile devices */
  transform: translateZ(0);
  -webkit-transform: translate3d(0,0,0);
  will-change: filter, box-shadow;
}

.button-primary.active {
  background: var(--color-purple, var(--color-purple));
  color: var(--color-teal, var(--color-teal));
  box-shadow: 0 0 24px var(--color-teal, var(--color-teal)),
              0 0 8px var(--color-purple, var(--color-purple));
}


/* --- Button Color Change Fast --- */
.button-primary:active {
  background: var(--color-purple, var(--color-purple));
  color: var(--color-teal, var(--color-teal));
  box-shadow: 0 0 24px var(--color-teal, var(--color-teal)),
              0 0 8px var(--color-purple, var(--color-purple));
}




